::selection{background:#5A4FCF;color:#fff;}
:focus{outline:0;outline:none;}
::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#2c2c2c!important;}
::-webkit-scrollbar{width:4px!important;}
::-webkit-scrollbar-thumb{background-color:#5A4FCF!important;border-radius:0!important;}
::-moz-selection {background: #0d5401 !important;color: #8798A5 !important;}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;900&display=swap');

* {
  font-family: 'Montserrat', sans-serif;
}

body {
  background-color:black;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.logo{
  width: 64px;
  list-style-type: none;
  float: left;
  margin-left: 1rem;
  margin-top: 0.5rem;
}

.nav {
  color: #5A4FCF;
  margin: 0;
  padding: 1rem;
  overflow: hidden;
  min-width:150px;
}

.nav li {
  list-style-type: none;
  float: left;
  margin-top: 0.3rem;
}


.nav li a {
  padding: 25px 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #5A4FCF;
  line-height: 40px;
  text-decoration: none;
}

.nav li a:hover {
  color: #6a61c9;
  cursor: pointer;
}

.btn, .btn-xs, .btn-md, .btn-lg {
  display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 1.5;
    color: #fff;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    background-color: rgba(240,238,255,0.2) !important;
    border: 1px solid rgba(0,0,0,0);
    border-radius: 4px;
    height: calc(1.8rem + 1.25rem + 2px);
    padding: 0rem calc(2rem + 1.5rem);
    font-size: 1rem !important;
    min-width: 5rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
}

.btn-lg {
  min-width: 8rem;
}

.btn:hover, .btn-xs:hover, .btn-md:hover, .btn-lg:hover {
  opacity: 0.6;
}

.bg-indigo, .hover-bg-indigo:hover {
  background-color: #5A4FCF !important;
}

subtitle{
  font-size: 0.8rem !important;
  margin-top: -5px;
}

/* App specifics */

.App {
  text-align: center;
}

.ImageCanvas{
  width: 30%;
}

.Floater{
  padding: 5%;
}

.TextareaParent{
  width: 100%;
  margin-bottom: 20px;
  margin-top: 30px;
}

.InformationText{
  font-size: 13px;
  margin: 0;
}

.CryptHolder {
  text-align: center;
    display: flex;
    flex-direction: column;
    padding: 3rem;
    align-items: center;
}

.DotArea {
  background: transparent;
  position: absolute;
  top: 5%;
  right: 5%;
  width: 20%;
  height: 30%;
  background-image: radial-gradient(#34c79e 3px, transparent 0);
  background-size: 40px 40px;
  background-position: -19px -19px;
}

.BrandName { 
  list-style-type: none;
    float: left;
    margin-top: 0.2rem;
    margin-left: 0.2rem;
    font-weight: 100;
    color: #e0f2ff;
}

.HiddenTextContainer {
  font-size: 1rem;
  font-weight: 600;
  width: 100%;
  height: 50vh;
  overflow-x: hidden; 
  overflow-y: scroll; 
}

.AboutSection {
  padding: 3rem;
  padding-top: 0;
}

.AboutSection p {
  font-size: 1rem;
}

.AboutSection img{
  width: 20%;
}

.ImageHolder{
  width: 100%;
  text-align: center;
}

.TextDisabled { 
  pointer-events: none;
  user-select: none;
  color: grey;
}

.LoadingFrame {
  position: absolute;
    background: black;
    width: 100vw;
    height: 100vh;
    display: flex;
    text-align: center;
    align-content: center;
    align-items: center;
    align-self: center;
}

.LoadingSpinner {
  align-items: center;
    text-align: center;
    flex: 1;
}

.InputTextArea{
  font-weight: 400;
  line-height: 1.5;
  color: white;
  background-color: transparent;
  background-clip: padding-box;
  border: 1px solid #B7C4CD;
  border-radius: 4px;
  background-color: rgba(240,238,255,0.1) !important;
  border-width: 0px !important;
  margin: "10px auto";
  padding: "10px";
  width: "200px";
  box-sizing: "border-box";
}

.InputTextArea{
  width: 95vw;;
}
.InputTextArea:focus{
border-color: #fff !important;
    box-shadow: 0 0 0 0.125em rgba(255,255,255,0.25) !important;
    outline: none;
}

@media only screen and (max-width: 600px) {
  .logo {
    width: 32px;
  }

  .nav li a {
    padding: 0px 10px;
    line-height: 30px;
  }

  .BrandName {
    margin-top: .5rem;
    margin-left: .2rem;
    font-size: 1.3rem;
  }

  .ImageCanvas {
    width: 90%;
  }

  .InputTextArea{
    width: 70vw;
  }

  .AboutSection img{
    width: 60%;
  }
}


